import { Node, Action, Inline } from '@ali/4ever-cangjie';
import { TagData } from '@ali/4ever-mo';
import { FormulaData } from './types';

export const FORMULA_SET_DATA = Symbol('formula-set-data');

export const FORMULA_SET_INJECTION = Symbol('formula-set-injection');

export interface SetFormulaDataPayload extends ActionPayload {
  data: Partial<TagData<FormulaData>>
}

export interface ActionPayload {
  node: Inline;
}

export interface SetInjectionPayload {
  key: string
  data: Record<string, any>
}

export function setFormulaData(node: Node, data: Partial<TagData<FormulaData>>): Action<SetFormulaDataPayload> {
  return {
    type: FORMULA_SET_DATA,
    payload: {
      node: node as Inline,
      data,
    }
  };
}

export function setFormulaInjection(key: string, data: Record<string, any>) {
  return {
    type: FORMULA_SET_INJECTION,
    payload: {
      key,
      data,
    },
  };
}
